/* @import url('https://fonts.googleapis.com/css2?family=Red+Hat+Display:ital,wght@0,300..900;1,300..900&display=swap'); */

html, body {
  height: 100%;
  margin: 0;
  /* font: 'red hat display' !important; */
}

#root {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

footer {
  margin-top: auto;
  background: rgba(0, 0, 0, 0.9); /* Solid dark teal background with some transparency */
  color: white; /* Text color */
  transition: all 0.3s ease;
  padding: 1.5rem 0;
  width: 100%;
  position: relative;
  z-index: 10;
}

.footer-links {
  font-size: 1rem; /* Set a base font size for the links */
  padding: 5px 0; /* Add some padding to the top and bottom */
}

.footer-links a {
  margin: 0 15px; /* Even spacing between links */
  text-decoration: none; /* Removes underline */
  color: white; /* Ensures link color is white */
  display: inline-block; /* Ensures margin is applied correctly */
  font-weight: 500;
  letter-spacing: 2px;
}

.footer-links a:hover {
  color: #7ecd73; /* Lighter teal for better contrast on hover */
  transform: translateY(-2px);
  transition: all 0.3s ease;

}

.btnu {
  display: inline-block; /* Make sure buttons align correctly */
  margin: 25px 0 0 0;
}

.btnu a {
  border-radius: 33%;
  margin: 5PX 5px; /* Small margin for buttons */
  border: 2px solid #fff;
  padding: 8px 8px; /* Adjust padding for consistency */
  color: white;
  font-size: 0.9rem;
}

.btnu a:hover {
  color: #236e65;
  background-color: #236e65;  
}

.iconu {
  color: white;
  transition: color 0.3s ease; 
}

/* Copyright section styles */
.copyright {
  background-color: black !important; /* Set background color */
  color: white; /* Ensure text is white */
  padding: 15px 0; /* Add padding */
  font-size: 0.8rem !important; /* Font size for copyright */
  font-weight: 500;
  letter-spacing: 1px;
}

/* Media Queries */
@media (max-width: 768px) {
  .btnu a {
    font-size: 0.4rem;
    margin: 5PX 5px; /* Further reduce font size */
    padding: 14px 8px;
  }
  .btnu {
    margin: 25px 0 0 0;
  }
  
  .iconu {
    font-size: 1rem; /* Adjust icon size */
  }
  .footer-links a {
    margin: 0 15px; /* Even spacing between links */
  }
  .footer-links {
    font-size: 0.7rem;
  }
  .copyright {
    font-size: 0.6rem !important; /* Font size for copyright */
    font-weight: 500;
    letter-spacing: 1px;
  }
}

@media (max-width: 480px) {
  .btnu a {
    font-size: 0.4rem; /* Even smaller font size */
    margin: 5PX 5px;
    padding: 14px 8px; 
  }
  .btnu {
    margin: 25px 0 0 0;
  }
  
  .iconu {
    font-size: 0.9rem; /* Adjust icon size */
  }
  .footer-links a {
    margin: 5px 15px; /* Even spacing between links */
  }
  .footer-links {
    font-size: 0.7rem;
  }
  .copyright {
    font-size: 0.6rem !important; /* Font size for copyright */
    font-weight: 500;
    letter-spacing: 1px;
  }
}
